"use"
import { useState, useEffect, useRef } from "react";
import { FileText } from "lucide-react";
import { useLatex } from "@/lib/LatexContext";
import { Skeleton } from "@/components/ui/skeleton"

let debounceTimer: NodeJS.Timeout;

export default function Pdf() {
  const { latexContent } = useLatex();
  const [pdfUrl, setPdfUrl] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const encodedSnipRef = useRef<HTMLTextAreaElement>(null);

  useEffect(() => {
    if (!latexContent) {
      setPdfUrl(null);
      setLoading(false);
      return;
    }

    // Debounce PDF generation
    setLoading(true);
    clearTimeout(debounceTimer);
    debounceTimer = setTimeout(() => {
      const encoded = encodeURIComponent(latexContent);
      const url = `https://latexonline.cc/compile?text=${encoded}`;
      setPdfUrl(url);
    }, 500); // wait 500ms before triggering

  }, [latexContent]);

  const openInOverleaf = () => {
    if (encodedSnipRef.current) {
      encodedSnipRef.current.value = latexContent || "";
      document.getElementById("overleaf_form")?.submit();
    }
  };

  return (
    <>
      <div className="flex justify-between">
        <div className="w-full flex items-center p-4 text-sm text-slate-400">
          <p>Preview</p>
        </div>
        <div className="transform m-2 z-50">
          <button
            onClick={openInOverleaf}
            type="button"
            className="bg-green-600 hover:bg-green-700 text-white font-semibold py-2 px-6 rounded-lg shadow-lg transition"
          >
            Overleaf
          </button>
        </div>
      </div>

      {latexContent ? (
        <div className="relative min-h-screen">
          {loading && (
            <div className="flex flex-col gap-4 p-8 items-center">
              {[...Array(12)].map((_, i) => (
                <Skeleton key={i} className="h-5 w-full" />
              ))}
            </div>
          )}
          {pdfUrl && (
            <iframe
              src={pdfUrl}
              width="100%"
              height="600px"
              title="LaTeX PDF Preview"
              className="border border-gray-300 rounded-lg"
              onLoad={() => setLoading(false)}
            />
          )}

          <form
            id="overleaf_form"
            action="https://www.overleaf.com/docs"
            method="post"
            target="_blank"
            className="hidden"
          >
            <textarea
              name="snip"
              ref={encodedSnipRef}
              defaultValue={latexContent}
            />
          </form>
        </div>
      ) : (
        <div className="bg-slate-200 h-full p-4">
          <div className="border-2 border-gray-300 h-full rounded-xl overflow-hidden ">
            <div className="flex flex-col justify-center items-center h-full text-slate-500">
              <FileText className="h-10 w-10" />
              <p className="text-2xl">pdf here</p>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
